.footer {
  background-color: #f8f8f8;
  padding: 66px 100px 120px 100px;
}

.footer-nav-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 68px;
}

.footer-nav-left-section {
  display: flex;
  align-items: center;
  text-decoration: none;
  cursor: pointer;
}

.footer-nav-img {
  margin-right: 20px;
}

.footer-nav-title {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 45px;
  line-height: 68px;
  color: #282828;
  margin: 0;
}

.footer-bottom-section {
  display: flex;
}

.footer-links-section {
  margin-right: 100px;
}

.footer-links-title {
  margin: 0;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.16em;
  color: #282828;
  margin-bottom: 10px;
}

.footer-links-unordered-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.footer-list-item-link {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 22px;
  margin-bottom: 5px;
}

.footer-list-item-link:last-child {
  margin-bottom: 0;
}

.footer-links-restaurants {
  margin-bottom: 49px;
}

.footer-social-links-section {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.footer-social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  background: black;
  width: 24px;
  height: 24px;
  border-radius: 50px;
}

.footer-social-link svg {
  color: white;
}

.footer-social-app-store {
  margin-bottom: 9px;
}

a {
  text-decoration: none;
  color: rgba(40, 40, 40, 0.68);
}
