.popular-locality-section {
  margin-bottom: 60px;
}

.popular-locality-title {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  line-height: 60px;
  color: #282828;
  margin-bottom: 30px;
}

.popular-locality-sub-title {
  color: black;
}

.popular-locality-container {
  background: #fff;
  border: 0.8px solid rgba(40, 40, 40, 0.2);
  border-radius: 15px;
  box-shadow: 0 2px 3px rgb(0 0 0 / 10%);
  padding: 12px 16px;
  cursor: pointer;
}

.popular-locality-name {
  color: #282828;
  font-size: 18px;
  line-height: 30px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 300;
  margin: 0;
}

.popular-locality-places {
  color: rgba(40, 40, 40, 0.7);
  font-size: 14px;
  letter-spacing: -0.015em;
  line-height: 21px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 300;
  margin: 0;
}

.popular-locality-see-more-container {
  padding: 8px;
}

.popular-locality-see-more-text {
  color: #282828;
  font-size: 20px;
  line-height: 33px;
  margin: 0;
  font-family: Poppins;
  font-style: normal;
  font-weight: 400;
}

.popular-locality-right-icon {
  color: rgba(40, 40, 40, 0.7);
  font-size: 25px;
}

.popular-locality-rotated-icon {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}
