.banner-bg {
  background-image: url("../../../assets/banner.png");
  background-size: cover;
  background-repeat: no-repeat;
  height: 400px;
  position: relative;
  margin: 0 0 40px 0;
}

.banner-bg::before {
  position: absolute;
  content: "";
  background: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
}

.banner-content {
  position: absolute;
  content: "";
  top: 62%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.header-logo {
  width: 80px;
  height: 80px;
}

.header-title {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 65px;
  line-height: 98px;
  color: #ffffff;
  margin-bottom: 0;
}

.header-sub-title {
  color: #ee2e2b;
}

.banner-desc {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 54px;
  color: #ffffff;
  margin-bottom: 35px;
  text-align: center;
}

.search-container {
  width: 60%;
  background-color: white;
  padding: 12px;
  border-radius: 15px;
  margin-bottom: 50px;
}

.banner-dropdown {
  border: none;
  outline: none;
  width: 200px;
  border-right: 2px solid rgba(0, 0, 0, 0.4);
  padding-right: 5px;
  cursor: pointer;
}

.location {
  width: auto;
  height: auto;
}

.city-name {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 300;
  font-size: 15px;
  line-height: 22px;
  color: #282828;
  margin: 0;
}

.banner-dropdown-container {
  position: relative;
}

.dropdown-content {
  padding: 5px 6px;
  background-color: white;
  border: 1px solid #707070;
  position: absolute;
  width: 100%;
  z-index: 1;
}

.dropdown-list-item {
  cursor: pointer;
  transition: 0.5s ease-out;
}

.dropdown-list-item:hover {
  background-color: #f8f8f8;
}

.input-search-container {
  padding-left: 5px;
}

.search-input {
  border: none;
  outline: none;
  width: 98%;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 300;
}

.search-input::placeholder {
  width: 100%;
  font-size: 15px;
  line-height: 22px;
  color: rgba(0, 0, 0, 0.7);
}

.search-icon {
  font-size: 24px;
}

@media screen and (max-width: 768px) {
  .banner-bg {
    height: 450px;
  }

  .search-container {
    width: 60%;
    margin-bottom: 40px;
  }

  .search-input:placeholder-shown {
    text-overflow: ellipsis;
  }

  .banner-dropdown {
    width: 150px;
  }
}

@media screen and (max-width: 576px) {
  .banner-dropdown {
    border-right: 0px;
  }

  .input-search-container {
    padding-left: 0px;
  }

  .banner-bg {
    height: 350px;
  }

  .banner-content {
    top: 65%;
    padding: 0 10px;
  }

  .header-logo {
    height: 45px;
    width: 45px;
  }

  .header-title {
    font-size: 36px;
    line-height: normal;
  }

  .banner-desc {
    font-size: 20px;
    line-height: normal;
  }

  .search-container {
    width: 90%;
  }

  .banner-dropdown {
    width: 130px;
  }
}
