.popular-items-section {
  margin: 0 0 60px 0;
}

.popular-items-title {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  line-height: 60px;
  color: #282828;
  margin-bottom: 10px;
}

.popular-items-description {
  color: #737b7d;
  font-family: "Poppins";
  font-style: normal;
  font-size: 16px;
  font-weight: 400;
}

.grid-container {
  display: grid;
  grid-template-columns: auto auto auto;
  column-gap: 40px;
  row-gap: 40px;
}

.grid-item {
  background-color: rgba(255, 255, 255, 0.8);
  font-size: 30px;
  overflow: hidden;
  border-radius: 20px;
}

.gridCards {
  width: 100%;
  height: 100%;
}

.gridCards:hover {
  cursor: pointer;
}
.gridCards img {
  transform: scale(1.1);
}
.gridCards img:hover {
  transform: scale(1.2);
  transition: all 0.3s linear;
}

.card {
  height: 100%;
}

.card-image-container {
  overflow: hidden;
}

.card-title {
  height: 100px;
  color: #383c3e;
  font-family: "Poppins";
  font-style: normal;
  font-size: 22px;
  font-weight: 500;
  height: 100px;
}

.card-text {
  font-family: "Poppins";
  font-style: normal;
  color: #737b7d;
  font-size: 18px;
  font-weight: 400;
  margin: 0;
}

.addBtn {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
}

.itemQuantityText {
  font-size: 16px;
}

.card-inside-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 1024px) {
  .grid-container {
    grid-template-columns: auto auto;
    row-gap: 30px;
    column-gap: 30px;
  }
}

@media screen and (max-width: 768px) {
  .grid-container {
    grid-template-columns: auto auto;
    row-gap: 30px;
    column-gap: 30px;
  }
}

@media screen and (max-width: 767px) {
  .grid-container {
    grid-template-columns: auto;
    row-gap: 30px;
    justify-content: center;
  }

  .gridCards {
    width: 100%;
    max-width: 312px;
  }

  .card-inside-container {
    flex-direction: column;
    justify-content: flex-start;
    align-items: start;
  }

  .popular-items-title {
    font-size: 30px;
    line-height: normal;
  }
}
