.custom-dropdown {
  position: relative;
  cursor: pointer;
  margin-right: 20px;
}

.custom-dropdown:last-child {
  margin-right: 0;
}

.dropdown-img {
  width: 25px;
  height: 25px;
}

.dropdown-btn {
  padding: 0px 10px;
  gap: 40px;
  width: 200px;
  height: 47px;
  border: 0.8px solid rgba(40, 40, 40, 0.3);
  border-radius: 5px;
}

.dropdown-option-name {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: #000000;
  margin: 0;
}

.dropdown-content {
  background-color: #fff;
  border: 1px solid #707070;
  padding: 5px 6px;
  position: absolute;
  width: 100%;
  z-index: 1;
}

.dropdown-list-item:hover {
  background-color: #f8f8f8;
}
