.loginPage {
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  background: #0000001a;
}

.imgContainer {
  position: relative;
  width: 50%;
  height: 100%;
}

.imgContainer img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.contentContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  margin: 25px 0;
}

.loginPage .contentContainer .formContainer {
  background-color: #fff;
  width: 300px;
  padding: 20px;
  border-radius: 16px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
}

.contentContainer h1 {
  text-align: center;
  color: #1e2833;
  text-transform: uppercase;
  margin-bottom: 20px;
  border-bottom: 4px solid #ff4584;
}

.actions {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.actions button {
  cursor: pointer;
  font: inherit;
  color: #000;
  background-color: transparent;
  border: 1px solid;
  border-radius: 4px;
  padding: 0.5rem 2.5rem;
  font-weight: 500;
}

.actions button:hover {
  color: #fff;
  border-color: #fff;
  background-color: #000;
}

.actions .toggle {
  margin-top: 1rem;
  background-color: transparent;
  border: none;
  padding: 0.15rem 1.5rem;
  color: #000000c0;
  font-weight: 500;
}

.actions .toggle:hover {
  background-color: transparent;
  color: #000000;
}

@media (max-width: 768px) {
  .imgContainer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .contentContainer {
    width: 100%;
    z-index: 1;
  }
}
