.today-items-section {
  margin: 0 0 40px 0;
}

.today-items-title {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  line-height: 60px;
  color: #282828;
  margin-bottom: 10px;
}

.today-items-section h3 {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 30px;
  line-height: 40px;
}

.today-items-section p {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 30px;
}

.today-items-img-container {
  overflow: hidden;
  width: auto;
  height: 300px;
  border-radius: 10px;
  background-color: gray;
}

.today-items-img-container img {
  width: 100%;
  height: 100%;
}

.today-items-hot-picks-container {
  margin-left: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px 0;
}

.today-items-btn-text {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 25px;
  line-height: 25px;
}

@media screen and (max-width: 768px) {
  .headingText {
    font-size: 36px;
  }

  .today-items-img-container {
    overflow: hidden;
    height: auto;
    border-radius: 10px;
  }

  .today-items-hot-picks-container {
    margin-left: 20px;
  }
}

@media screen and (max-width: 576px) {
  .today-items-title {
    font-size: 30px;
    line-height: normal;
  }

  .today-items-hot-picks-container {
    margin: 0;
  }

  .today-items-section h3 {
    font-size: 26px;
    line-height: normal;
  }

  .today-items-section p {
    font-size: 20px;
    line-height: normal;
  }

  .today-items-btn-text {
    font-size: 20px;
    line-height: normal;
  }

  .today-items-hot-picks-container {
    padding: 0;
  }
}
